import React, { Component } from 'react';
import Pie from '../components/Pie';
import '../App.js';
import PropTypes from 'prop-types';

class Progressbar2 extends React.Component {
  render() {
    return (
      <div>
        <div
          className="box"  
        >
          <Pie
            color="#01579B"
            width={75}
            progress={.75}
			// ca marche avec this.state.division2 ou division mais ca affiche que state est null
          />
        </div>
      </div>
    );
  }
 
}

export default Progressbar2;